/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"18 Years Ago","date":"06 Sep 2001","slug":"/2001-09-06-18-years-ago/","tags":"Y2001"},"html":"<p>Today is our 18th wedding anniversary. Mama thought of it first thing this morning. I'm sure I would have too at some point during the day.</p>"},"next":{"fields":{"title":"Don't Make Me Go","date":"05 Sep 2001","slug":"/2001-09-05-dont-make-me-go/"}},"previous":{"fields":{"title":"Too Pooped to Party","date":"07 Sep 2001","slug":"/2001-09-07-too-pooped-to-party/"}}},{"node":{"fields":{"title":"Don't Make Me Go","date":"05 Sep 2001","slug":"/2001-09-05-dont-make-me-go/","tags":"Y2001"},"html":"<p>There's a first time for everything. Christopher's at a substitute 'Tagesmutter' for 3 weeks, as Heide is on vacation. This afternoon, for the first time at day care, he didn't want to come home. He had to show off the tricycle (that he can't quite pedal yet) and the mini-slide (that he can climb and slide down by himself). </p>\n<p>Retraction: the hotel linked to yesterday isn't really that bad, it's merely ugly, inside and out. But it's the biggest hotel in Munich, so when Mama has to attend big events in Munich, she usually gets put there.(<i>Later</i> Mama tells me that they've remodelled since I was there, so it's not even ugly on the inside anymore. Just from the outside.)</p>"},"next":{"fields":{"title":"No Broadband","date":"04 Sep 2001","slug":"/2001-09-04-no-broadband/"}},"previous":{"fields":{"title":"18 Years Ago","date":"06 Sep 2001","slug":"/2001-09-06-18-years-ago/"}}},{"node":{"fields":{"title":"No Broadband","date":"04 Sep 2001","slug":"/2001-09-04-no-broadband/","tags":"Y2001"},"html":"<p>I should have had this one yesterday... what happens when you try to deposit foreign checks at the <a href=\"http://www.xdude.com/flashed-mar2001.htm\">bank</a> (Flash alert! with music!) (via <a href=\"http://www.stormwerks.com/linked/\">/usr/bin/girl</a>)</p>\n<p>Despite my <a href=\"http://shanson.editthispage.com/2001/08/24\">hopes</a>, the Telekom sent us a rejection notice for DSL after all, even though it's supposedly available in Jesteburg. They don't say why, just that it's 'technically infeasible at this time'. I suspect that we're too far from the \"Central Office\", so we're stuck with ISDN until something better comes along (the Telekom is supposedly testing lower bandwidth DSL that will work over longer distances). Our T-Online ISDN flatrate expires in October, but we can switch our ISDN line to <a href=\"http://www.ewetel.de\">Ewetel</a> and use their flatrate. </p>\n<p>A program I use every day: <a href=\"http://phpmyadmin.sourceforge.net/\">phpMyAdmin</a> has been updated with many improvements. (via <a href=\"http://www.schockwellenreiter.de\">SWR</a>)</p>\n<p>Mama just called to say that she's just checked into the worst hotel she's experienced in 8 years of professional travelling. I guess she had to tell me, since usually I'm along when she experiences <a href=\"http://www.telehotel.de/sheraton_muenchen/foto.htm\">bad hotels</a>. I'll be discrete and not mention <a href=\"http://www.hildesheim.de/\">where</a> she is staying tonight.</p>"},"next":{"fields":{"title":"Borders are a Pain","date":"03 Sep 2001","slug":"/2001-09-03-borders-are-a-pain/"}},"previous":{"fields":{"title":"Don't Make Me Go","date":"05 Sep 2001","slug":"/2001-09-05-dont-make-me-go/"}}}],"pathPrefix":"","first":false,"last":false,"index":978,"pageCount":1101}}

/***/ })

